import { Type } from '@angular/core';
import { ListItemComponent } from '@sofico-framework/ui-kit/types';
import { SortingOrderConfig } from '@sofico-framework/utils';

/**
 * Configuration/builder to build an overviewList config
 * This object contains all the configuration regarding searching and
 * sorting of an object
 */
export class OverviewListConfig<T> {
  functionalProps: {
    selector: (entity: T) => any;
    label: string;
    searchable: boolean;
    sortable: boolean;
    plainSort: boolean;
  }[] = [];
  initialSorting: SortingOrderConfig<T> = {
    prop: null
  };
  dynamicRowComponent: Type<ListItemComponent<T>> = null;
  private initialSortingFuncPropRef = null;

  addFunctionalProp(selector: (entity: T) => any): OverviewListConfig<T> {
    this.functionalProps.push({
      label: null,
      selector,
      searchable: false,
      sortable: false,
      plainSort: false
    });
    return this;
  }

  withSearch(): OverviewListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.searchable = true;
    return this;
  }

  /** @deprecated use {@link withNaturalSorting}  */
  withSorting(label: string): OverviewListConfig<T> {
    return this.withNaturalSorting(label);
  }

  withNaturalSorting(label: string): OverviewListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.label = label;
    propToUpdate.sortable = true;
    propToUpdate.plainSort = false;
    if (this.initialSortingFuncPropRef) {
      this.initialSortingFuncPropRef.plainSort = propToUpdate.plainSort;
    }
    return this;
  }

  withPlainSorting(label: string): OverviewListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    propToUpdate.label = label;
    propToUpdate.sortable = true;
    propToUpdate.plainSort = true;
    if (this.initialSortingFuncPropRef) {
      this.initialSortingFuncPropRef.plainSort = propToUpdate.plainSort;
    }
    return this;
  }

  asInitialSorting(order: 'asc' | 'desc'): OverviewListConfig<T> {
    const propToUpdate = this.functionalProps[this.functionalProps.length - 1];
    this.initialSorting = {
      prop: propToUpdate.selector,
      order,
      plainSort: propToUpdate.plainSort
    } as SortingOrderConfig<T>;
    this.initialSortingFuncPropRef = propToUpdate;
    return this;
  }

  setDynamicRowComponent(
    component: Type<ListItemComponent<T>>
  ): OverviewListConfig<T> {
    this.dynamicRowComponent = component;
    return this;
  }
}
